import React, { useState, useEffect } from 'react';
import styled, { keyframes, css } from 'styled-components';
import { ViewDefault, TextDefualt } from '@views/atoms/Custom';

type itemType = {
	component: '' | React.ReactNode;
	path: string;
	fileName: string;
	title: string;
	author: string;
	date: string;
	categories: string;
	tags: string;
	toc: string;
	img: string;
};

type BlogCard = {
	item: itemType;
	index: number;
	onClickBlogItem: (i: itemType) => void;
};
function BlogCard({ item, index, onClickBlogItem }: BlogCard) {
	const [isMouseOver, setIsMouseOver] = useState(false);

	const onMouseOver = () => {
		setIsMouseOver(true);
	};
	const onMouseLeave = () => {
		setIsMouseOver(false);
	};
	return (
		<View.Container onClick={() => onClickBlogItem(item)}>
			<View.InnderContainer
				onMouseOver={onMouseOver}
				onMouseLeave={onMouseLeave}
				mouseOver={isMouseOver}
			>
				<View.ImgBox>
					<img
						style={{ width: '100%', height: '100%' }}
						src={item.img}
						alt="none"
					/>
				</View.ImgBox>
				<View.TextBox>
					<Text.Title>{item.title}</Text.Title>
					<Text.SubTitle>{item.date + '  |  ' + item.categories}</Text.SubTitle>
				</View.TextBox>
			</View.InnderContainer>
		</View.Container>
	);
}

export default BlogCard;

const View = {
	Container: styled(ViewDefault)`
		width: 50%;
		height: 470px;
		background-color: rgb(248, 249, 250);
		padding: 0px 10px;
	`,
	InnderContainer: styled(ViewDefault)`
		height: 450px;
		width: 100%;
		border-radius: 6px;
		overflow: hidden;
		box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 10px 0px;
		margin: 0px 0;
		transition: all 0.25s;
		${(props: ChangeMargin) => {
			if (!props.mouseOver) {
				return css`
					margin-top: 10px;
					box-shadow: rgba(0, 0, 0, 0.04) 0px 0px 10px 0px;
				`;
			}
		}}
	`,
	ImgBox: styled(ViewDefault)`
		width: 100%;
		height: 300px;
		background-color: #999999;
		cursor: pointer;
	`,
	TextBox: styled(ViewDefault)`
		width: 100%;
		height: 150px;
		padding: 20px;
		background-color: white;
	`,
};

const Text = {
	Title: styled(TextDefualt)`
		font-size: 30px;
		color: #333333;
		cursor: pointer;
		font-weight: bold;
	`,
	SubTitle: styled(TextDefualt)`
		font-size: 20px;
		color: #eceef0;
	`,
};

type ChangeMargin = {
	mouseOver: boolean;
};
